* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "josefin Sans", "Arial Narrow";
}

.header {
  width: 100%;
  line-height: 60px;
  background-color: black;
  display: flex;
  align-items: center;
  padding-left: 50px;
  color: white;
  box-shadow: 5px 3px 15px -5px rgba (0, 0, 0, 0.3);
  font-size: 22px;
}

.headet_text {
  color: rgba(255,255,255, 0.87 );
  padding-left: 4px;
  opacity: 1;
  display: inline-block;
  font-family: 'Product Sans',Arial,sans-serif;
  font-size: 22px;
  line-height: 30px;
  padding-left: 8px;
  position: relative;
  top: -1.5px;
  vertical-align: middle;
}

.madewith_text {
  padding-left: 55%;
}
.main_note {
  width: 500px;
  padding: 15px 10px 5px 10px;
  margin: 20px auto;
}

.my_notes h1 {
  color: #34495e;
}
.my_notes p {
  color: grey;
}

form {
  min-height: 50px;
  color: #ecf0f1;
  padding: 15px 10px 5px 10px;
  box-shadow: 3px 5px 15px -5px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  position: relative;
  background-color: aliceblue;
}
input {
  width: 100%;
  line-height: 30px;
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 1.5rem;
  margin: 10px 0;
  font-weight: bold;
  font-family: auto;
}

textarea {
  width: 100%;
  line-height: 30px;
  background: transparent;
  border: none;
  outline: none;
  font-size: 1rem;
  margin: 10px 0;
  font-size: larger;
  font-family: auto;
}

.MuiButton-root {
  width: 30px;
  height: 50px;
  position: absolute;
  bottom: -28px;
  left: 400px;
  cursor: pointer;
  background-color: #fff !important;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
  border-radius: 50% !important;
}

.MuiButton-root .plus_sign {
  font-size: 45px !important;
  font-weight: 900;
  color: #f4b400;
  cursor: pointer;
  z-index: 222;
  background: transparent;
}

.MuiButton-root:hover {
  background: #f4b400 !important;
}
.MuiButton-root:hover .plus_sign {
  color: #fff;
  background: transparent;
}

.note {
  background: #fff;
  border-radius: 7px;
  box-shadow: 0 2px 5px #ccc;
  padding: 10px;
  width: 240px;
  margin: 16px;
  float: left;
  background-color: lightgreen;
}
.note h1 {
  font-size: 1.1em;
  margin-bottom: 6px;
}
.note p {
  font-size: 1.1em;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.note .btn {
  position: relative;
  float: right;
  margin-right: 10px;
  color: #f5ba13;
  border: none;
  width: 50px;
  height: 50px;
  background: transparent;
  cursor: pointer;
  outline: none;
  box-shadow: 3px 3px 15px -10px rgba(0, 0, 0, 0.3);
  border-radius: 50%;
}

.deleteIcon {
  width: 3.7em;
  height: 0.7em;
}
.note .btn:hover {
  background-color: gray;
}

.note .btn:hover .deleteIcon {
  color: #fff;
}

.footer p {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
  color: white;
  margin-top: auto;
  font-size: 1rem;
  padding: 1rem 0;
  font-weight: bold;
  background-color: black;
}

